* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.box-form {
  background-color: #00f;
}

#search-form {
  width: 100%;
  background-color: #00f;
  justify-content: center;
  align-items: center;
  margin-bottom: 16px;
  padding: 20px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 2px 4px -1px rgba(0, 0, 0, .2), 0 4px 5px rgba(0, 0, 0, .14), 0 1px 10px rgba(0, 0, 0, .12);
}

.search-form input {
  width: 300px;
  height: 30px;
  border: #fafafa;
  border-radius: 5px;
  outline: none;
  margin-right: 5px;
  padding: 12px;
  font-size: 14px;
}

.search-form button {
  width: 100px;
  height: 30px;
  background-color: #d3d3d3;
  border: #fafafa;
  border-radius: 5px;
  outline: none;
  font-size: 14px;
}

.load-more {
  width: 100px;
  height: 30px;
  background-color: #d3d3d3;
  border: #fafafa;
  border-radius: 5px;
  outline: none;
  margin-left: auto;
  margin-right: auto;
  font-size: 14px;
  display: flex;
}

.gallery {
  max-width: 1200px;
  grid-gap: 12px;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-auto-rows: 210px;
  justify-content: center;
  margin: 80px auto 13px;
  display: grid;
}

.gallery__item {
  text-decoration: none;
}

.photo-card {
  width: 100%;
  height: 100%;
  border-radius: 5px;
  box-shadow: 0 2px 1px 1px rgba(0, 0, 0, .2);
}

img {
  object-fit: cover;
  height: 170px;
  width: 100%;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  display: block;
}

.info {
  height: 40px;
  justify-content: space-evenly;
  align-items: center;
  padding: 15px;
  font-size: 12px;
  display: flex;
}

.info-item {
  color: #000;
  text-align: center;
  flex-direction: column;
  justify-content: center;
  gap: 5px;
  list-style: none;
  display: flex;
}

